import React from 'react';
import { LinkButton } from '../ButtonElements';
import { Column1, Column2, TextWrapper, TopLine, Heading, Subtitle, BtnWrap, InfoContainer, InfoRow, InfoWrapper, ImgWrap, Img } from './InfoElements';

const InfoSection = ({lightBg, id, imgStart, topLine, lightText, headline, darkText, description, buttonLabel, img, alt, primary, dark, dark2, linkTo}) => {
    return (
        <>
          <InfoContainer lightBg={lightBg} id={id}>
              <InfoWrapper>
                  <InfoRow imgStart={imgStart}>
                      <Column1>
                          <TextWrapper>
                              <TopLine>{topLine}</TopLine>
                              <Heading lightText={lightText}>{headline}</Heading>
                              <Subtitle darkText={darkText}>{description}</Subtitle>
                              <BtnWrap>
                                  <LinkButton to={'/' + linkTo } 
                                  duration={500}
                                  offset={-80}
                                  dark={dark ? 1 : 0} 
                                  dark2={dark2 ? 1 : 0} 
                                  primary={primary ? 1 : 0}
                                  >
                                  {buttonLabel}</LinkButton>
                              </BtnWrap>
                          </TextWrapper>
                      </Column1>
                      <Column2>
                          <ImgWrap>
                              <Img src={img} alt={alt}/>
                          </ImgWrap>
                      </Column2>
                  </InfoRow>
              </InfoWrapper>
          </InfoContainer>  
        </>
    )
}

export default InfoSection;
